.wrapper {
  padding: 25px 0px;
  margin-bottom: 80px;
}

.CarList {
  display: flex;
  justify-content: center;
  row-gap: 29px;
  column-gap: 50px;
  flex-wrap: wrap;
  list-style: none;
  width: 100%;
}

.loadMoreBtn {
  width: 200px;
  padding: 12px 0;
  color: #fff;
  font-family: 'Manrope', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  border-radius: 12px;
  background: #3470ff;
  border: none;
  cursor: pointer;
  box-shadow: 0px 3.43693px 3.43693px 0px rgba(0, 0, 0, 0.25);

  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  display: flex;
  margin: 0 auto;
  justify-content: center;
  margin-top: 60px;
}

.loadMoreBtn:hover {
  background: #0b44cd;
}
