.CardWrapper {
  width: 274px;
  height: 426px;
  display: flex;
  flex-direction: column;
}

.CarImgWrap {
  position: relative;
  width: 100%;
}

.CarImg {
  display: block;
  width: 100%;
  max-width: 100%;
  width: 274px;
  height: 268px;
  border-radius: 14px;
  object-fit: cover;
  object-position: center center;
}

.favoriteIconBtn {
  position: absolute;
  margin: 0;
  top: 14px;
  right: 14px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.transparentFavoriteIconBtn {
  display: block;
  color: rgb(255 255 255 / 80%);
  width: 18px;
  height: 18px;
}

.blueFavoriteIconBtn {
  display: block;
  color: #3470ff;
  width: 18px;
  height: 18px;
}

.InfoWrapper {
  width: 100%;
  align-items: center;
}

.MainInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 14px;
  margin-bottom: 8px;
  font-weight: 500;
  line-height: 24px;
}

.makeModelYearWrapper {
  display: flex;
}

.CarInfo {
  display: flex;
  width: 100%;
  gap: 5px;
}

.CarText {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #121417;
  font-size: 16px;
  line-height: 24px;
  font-family: Manrope, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.makeCarText {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #121417;
  font-size: 16px;
  line-height: 24px;
  font-family: Manrope, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin-right: 6px;
}

.yearCarText {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #121417;
  font-size: 16px;
  line-height: 24px;
  font-family: Manrope, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin-left: 6px;
}

.ModelBlue {
  color: #3470ff;
  font-size: 16px;
  font-family: Manrope, sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.SecondaryCarText {
  color: rgba(18, 20, 23, 0.5);
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.learnMoreBtn {
  width: 274px;
  padding: 12px 0;
  color: #fff;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  border-radius: 12px;
  background: #3470ff;
  border: none;
  cursor: pointer;
  outline: none;
  margin-top: 28px;
  box-shadow: 0px 3.43693px 3.43693px 0px rgba(0, 0, 0, 0.25);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.learnMoreBtn:hover {
  background: #0b44cd;
}
